html {
  font-family: "Opensans", sans-serif;
  scroll-behavior: smooth;
}

body {
  font-size: 16px;
  margin: 0;
  padding: 0;
  color: var(--text-color-black);
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  border: 0 !important;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  object-fit: cover;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
  color: var(--text-color-black);
}

h2 {
  padding: 0;
  margin: 0;
  font-size: 42px;
  line-height: 1.1;
  font-weight: 900;
  color: var(--text-color-black);
}

.container {
  max-width: 1135px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn-reset {
  font-family: "Opensans", sans-serif;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.red-text {
