  padding: 31px 0 17px 0;
}

.logo-link {
  display: flex;
  align-items: center;
}

.logo-link__name-wrapper {
  display: flex;
  flex-direction: column;
}

.logo-link__img {
  margin-right: 13px;
}

.logo-link__subname {
  display: inline-block;
